@import 'src/assets/scss/colors.module.scss';
@import 'src/assets/scss/mixins.scss';

.light {

  &__item {
    display: flex;
    align-items: center;
    padding: 22px 21px;
    background: #ffff;
    box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.05);
    border-radius: 12px;
    margin-bottom: 28px;
    cursor: pointer !important;
    
    @include media('mobile') {
      flex-direction: column;
      align-items: flex-start;
      gap: 16px;
    }

    &__info {
      
      display: flex;
      

      @include media('mobile') {
        justify-content: space-between;
        gap: 16px;
        width: 100%;
      }
    }

    &__name {
      font-size: 1em;
      font-weight: 500;
      flex: 1;
    }

    &__date-created {
      font-size: 12px;
      display: flex;
      align-items: center;
      color: #C1C1C4;
      span {
        margin-right: 13px;
      }
    }

    &__status {
      font-size: 12px;
      font-weight: 500;
      color: #56CCF2;
      padding: 5px 8px;
      border: 1px solid #56CCF2;
      border-radius: 8px;
      margin-left: 27px;
      margin-right: 33px;

      @include media('mobile') {
        margin: 0;
      }
    }

    &__arrow-right {
      color: $orange;
    }
  }
}